import React, { Component } from 'react'
// 获得奖品
 class Ownobj extends Component {
    constructor(props){
        super(props);
        this.state={
            ownobj:[],
            // username:'1111'
        }
    }
    getData = () => {
        fetch('https://www.sevenhero.cn:2050/ownobj').then(res => {
          if (!res.ok) {
            throw new Error(`${res.status} : ${res.statusText}`)
          } else {
            return res.json()
          }
        }).then(d => {
          console.log(d);
          this.setState({
           ownobj: d
          })
          console.log(this.state.ownobj)
        }).catch(err => {
          console.error(err)
        })
      }
      componentDidMount = () => {//在render执行之后就执行，且只执行一次
        this.getData();
        // console.log(localStorage.getItem('userinfo').username)
        // this.setState({
        //   username:localStorage.getItem('userinfo').username
        // })
      }
    render() {
        return (
          <div
            style={{
              width: '100%',
              height: '1200px',
              border: '1px solid white',
              backgroundColor: '	LightSteelBlue'
            }}
          >
            <div
              style={{
                width: '20%',
                height: '1200px',
                border: '1px solid #EEE9E9',
                backgroundColor: '#EEE9E9'
              }}
            >
    
              <div className="user_left" style={{ backgroundColor: 'white', width: '50%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }} onClick={() => { this.props.history.push('/user') }}>用户信息</div>
              <div className="user_left" style={{ backgroundColor: '	white', width: '60%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }}  onClick={() => { this.props.history.push('/object') }}>奖品设置</div>
              <div className="user_left" style={{ backgroundColor: '#CDC8B1', width: '70%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }}  >获奖详情</div>
              <button className="user_left" style={{ backgroundColor: '#CD0000', width: '40%', height: '3%', marginTop: '70%', fontSize: '1.2rem' }}  
              onClick={()=>{
                if(window.confirm('确定退出？')){
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                this.props.history.push('/login');

              }}}
              >退出账号</button>
            </div>
    
    
            <div
              style={{
                width: '80%',
                height: '60px',
                borderTop: '1px solid LightSteelBlue',
                borderBottom: '1px solid gray',
                marginLeft: '20%',
    
                marginTop: '-1200px'
              }}
            >
              <div
                style={{
                  width: '12%',
                  height: '40px',
                  // border: '1px solid blue',
    
                  marginLeft: '40%',
                  backgroundColor: '#E6E6FA',
    
    
                  // float:'top',
                  // zIndex:'1',
                  marginTop: '-30px',
                  fontSize: '1.2rem',
                  paddingTop: '40px',
                  paddingLeft: '60px',
    
                }}
    
              >获奖名单</div>
              
            </div>
    
            <div
              style={{
                width: '80%',
                height: '1130px',
                border: '1px solid LightSteelBlue',
                marginLeft: '20%',
                overflow:'auto'
                // float:'left',
                // marginTop:'-1300px'
              }}
            >
    
              <div style={{ height: '60px', borderBottom: '1px solid black', }} >
                <div
                  style={{ width: '10%', marginLeft: '10%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}>奖品名称</div>
                <div
                  style={{ width: '10%', float: 'left', marginTop: '-63px', marginLeft: '35%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}
                >游戏昵称</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-63px', marginLeft: '60%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}
                >获奖时间</div>       
              </div>
              
              {
                this.state.ownobj.map((data, index) => (
              <div style={{ height: '50px', borderBottom: '1px solid black', color: 'gray' }} >
                   <div
                  style={{ width: '10%', marginLeft: '10%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}>{data.obj}</div>
                <div
                  style={{ width: '10%', float: 'left', marginTop: '-63px', marginLeft: '35%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}
                >{data.gamename}</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-63px', marginLeft: '60%', fontSize: '1.2rem', height: '35px', paddingTop: '25px' }}
                >{data.time}</div>  
                  </div>
                ))
                } 
            </div>
                
    
    
            {/* </div> */}
    
    
          </div>
        )
      }
}
export default Ownobj;