import React, { Component } from 'react'
import check from './check';
// 登录页

class Login extends Component {
    constructor(props) {
        super(props);
        this.state = {
            username: '',
            password: ''
        }
    }
    componentDidMount=()=>{
        if (localStorage.getItem('token')) {
            try {
              let u = localStorage.getItem('userinfo')
              if (u) {
                let uinfo = JSON.parse(u)
                if (uinfo.expires + uinfo.time > Date.now()) {
                    this.props.history.push('/user');
                }
              }
            } catch(err) {
              console.error(err)
            }
          }
      
    }
    setUserInfo(event, key) {
        let obj = {};
        obj[key] = event.target.value;
        this.setState(obj);
    }
    render() {
        return (
            <div style={{
                width: '100%',
                height: '1200px',
                border: '1px solid ',
                backgroundColor:'	LightSteelBlue'
            }}
            >
                <h1
                style={{
                    marginLeft:'25%',
                    color:'black',
                    fontSize:'3rem'
                }}
                >天刀DKP后台管理</h1>
                <div style={{
                    width:'30%',
                    height:'35%',
                    marginTop:'10%',
                    marginLeft:'33%',
                    // border:'1px solid black',
                    backgroundColor:'#F5F5F5',
                    fontSize:'1.3rem',
                   
                }}
                    >
                        账号：<input 
                        style={{
                            width:'70%',
                            height:'10%',
                            backgroundColor:'#FFF5EE',
                            marginTop:'15%',
                            marginLeft:'3%',
                        }}
                        onInput={(event) => {
                            this.setUserInfo(event, 'username');
                        }}
                        required
                        /><br/>
                      密码：<input
                      type="password" 
                        style={{
                            width:'70%',
                            height:'10%',
                            backgroundColor:'#FFF5EE',
                            marginTop:'15%',
                            marginLeft:'3%'
                        }}
                        onInput={(event) => {
                            this.setUserInfo(event, 'password')
                        }}
                        required
                        ></input>
                        <button
                        style={{
                            color:'gray',
                            width:'100px',
                            height:'50px',
                            marginTop:'8%',
                            marginLeft:'38%'
                        }}
                        onClick={()=>{
                            let p=new check();
                            p.login(this.state.username, this.state.password, () => {
                                // localStorage.setItem('name',`${this.state.username}`);
                                // 登录成功时，跳转页面
                                this.props.history.push('/user');
                            });

                        }
                          
                            
                        }
                        >登录</button>

                </div>
            </div>
        )
    }
}

export default Login
