import React, { Component } from 'react'
import { NavLink } from 'react-router-dom';
import Left from './Left';
import './User.css'

// 用户列表
class User extends Component {
  constructor(props) {
    super(props);
    this.state = {
      user: []
    }
  }
  getData = () => {
    fetch('https://www.sevenhero.cn:2050/users').then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        user: d
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
  }
  delData=(e)=>{
    if(window.confirm('确定删除？')){
    let id=e.target.name;
    console.log(id);
    fetch('https://www.sevenhero.cn:2050/users/'+id, {
      method: 'DELETE',
    }).then(async res => {

      if (!res.ok) {
        let errtext = await res.text()
        throw new Error(`${res.status} : ${errtext}`)
      } else {
        //  return res//成功后的返回值
      }

    }).then(d => {
      //'提交成功'
      alert('删除成功')
      this.getData();

    }).catch(err => {
      alert('错误')
    })
  }
  }
  componentDidMount = () => {//在render执行之后就执行，且只执行一次
    this.getData();
  }
  render() {
    return (
      <div
        style={{
          width: '100%',
          height: '1200px',
          border: '1px solid white',
          backgroundColor: '	LightSteelBlue'
        }}
      >
        <div
          style={{
            width: '20%',
            height: '1200px',
            border: '1px solid #EEE9E9',
            backgroundColor: '#EEE9E9'
          }}
        >

          <div className="user_left" style={{ backgroundColor: '	#CDC8B1', width: '50%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }} >用户信息</div>
          <div className="user_left" style={{ backgroundColor: 'white', width: '60%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }} onClick={() => { this.props.history.push('/object') }}>奖品设置</div>
          <div className="user_left" style={{ backgroundColor: 'white', width: '70%', height: '3%', marginTop: '20%', fontSize: '1.2rem', paddingLeft: '26%', paddingTop: '5%' }} onClick={() => { this.props.history.push('/ownobj') }} >获奖详情</div>
          <button className="user_left" style={{ backgroundColor: '#CD0000', width: '40%', height: '3%', marginTop: '70%', fontSize: '1.2rem' }}  
              onClick={()=>{
                if(window.confirm('确定退出？')){
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                this.props.history.push('/login');

              }}}
              >退出账号</button>
        </div>


        <div
          style={{
            width: '80%',
            height: '60px',
            marginLeft: '20%',
            borderTop: '1px solid LightSteelBlue',
            borderBottom: '1px solid gray',
            marginTop: '-1200px'
          }}
        >
          <div style={{width: '12%', height: '40px',marginLeft: '20%',backgroundColor: '#E6E6FA',marginTop: '-30px',fontSize: '1.2rem',paddingTop: '40px', paddingLeft: '60px',}}>用户列表</div>
          <div
            style={{width: '12%',height: '40px', marginLeft: '50%', float: 'left',backgroundColor: 'white',marginTop: '-83px',fontSize: '1.2rem',paddingTop: '40px',paddingLeft: '60px',}}
            onClick={() => {
              this.props.history.push('/adduser')
            }}
          >添加用户</div>
        </div>

        <div style={{ width: '80%',height: '1130px', border: '1px solid LightSteelBlue', marginLeft: '20%',overflow:'auto' }}>


          <div style={{height: '60px', borderBottom: '1px solid black', }} >
            <div
              style={{width: '10%',marginLeft: '10%',fontSize: '1.2rem', height: '35px',paddingTop: '25px'}}>游戏昵称</div>
            <div
              style={{width: '10%',float: 'left', marginTop: '-63px', marginLeft: '25%', fontSize: '1.2rem',height: '35px', paddingTop: '25px' }}
            >历史积分</div>
            <div style={{width: '10%',float: 'left', marginTop: '-63px', marginLeft: '40%',fontSize: '1.2rem', height: '35px',paddingTop: '25px'}}
            >出勤次数</div>
            <div style={{ width: '10%',float: 'left',marginTop: '-63px',marginLeft: '55%',fontSize: '1.2rem',height: '35px',paddingTop: '25px'}}
            >工会名称</div>
            <div style={{width: '10%',float: 'left',marginTop: '-63px',marginLeft: '70%',fontSize: '1.2rem',height: '35px',paddingTop: '25px'}}
            >可抽奖数</div>
            <div style={{ width: '10%',float: 'left',marginTop: '-63px',marginLeft: '85%',fontSize: '1.2rem',height: '35px', paddingTop: '25px'}}
            >操作</div>
          </div>

          {
            this.state.user.map((data, index) => (
              <div style={{ height: '50px', borderBottom: '1px solid black', color: 'gray' }} >
                <div style={{ width: '10%', marginLeft: '11%', fontSize: '1.0rem', height: '30px', paddingTop: '20px' }}>{data.gamename}</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-55px', marginLeft: '26%', fontSize: '1.0rem', height: '30px', paddingTop: '25px' }}>{data.history}</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-55px', marginLeft: '41%', fontSize: '1.0rem', height: '30px', paddingTop: '25px' }} >{data.count}次</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-55px', marginLeft: '56%', fontSize: '1.0rem', height: '30px', paddingTop: '25px' }} >{data.orgation}</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-55px', marginLeft: '71%', fontSize: '1.0rem', height: '30px', paddingTop: '25px' }}>{data.can}</div>
                <div style={{ width: '10%', float: 'left', marginTop: '-55px', marginLeft: '84%', fontSize: '1.0rem', height: '30px', paddingTop: '25px' }}><button style={{ marginRight: '5px' }} name={data.id} onClick={this.delData}>删除</button><button  onClick={()=>{this.props.history.push({pathname:'/update',id:`${data.id}`})}} >编辑</button></div>
              </div>
            ))}


        </div>



        {/* </div> */}


      </div>
    )
  }
}

export default User
