import React, { Component } from 'react'
// 更新用户
 class updateUser extends Component {
    constructor(props){
        super(props);
        this.state={
          gamename:'',
          orgation:'',
          history:'',
          count:'',
        }
    }
    putData=()=>{
      let data={
        id:this.props.location.id,
        history:this.state.history,
        count:this.state.count,
      };
      console.log(data)
  
      console.log(data);
      fetch('https://www.sevenhero.cn:2050/updateobject', {
              method: 'POST',
              headers: {
                  'content-type': 'text/plain'
              },
              body: JSON.stringify(data)
          }).then(async res => {
  
              if (!res.ok) {
                  let errtext = await res.text()
                  throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
              } else {
                  //  return res//成功后的返回值
                 return  res;
              }
  
          }).then(d => {
              //'提交成功'
              if(d.status===200){
              
              alert('更新成功')
              }
              else{
                alert('更新失败')
              }
              // this.setState({
              //   obj:'',
              //   count:'',
              //   probability:'',
              //   src:'',
              //   files:[]
                
              // })
             
          }).catch(err => {
              alert(err.message)
          })
    }
    componentDidMount=()=>{
      let id=this.props.location.id;
      fetch('https://www.sevenhero.cn:2050/users/'+id).then(res => {
      if (!res.ok) {
        throw new Error(`${res.status} : ${res.statusText}`)
      } else {
        return res.json()
      }
    }).then(d => {
      console.log(d);
      this.setState({
        gamename:d[0].gamename,
        orgation:d[0].orgation,
        history:d[0].history,
        count:d[0].count,
      })
      // console.log(this.state.user)
    }).catch(err => {
      console.error(err)
    })
    }
    render() {
      console.log(this.props);
        return (
            <div
            style={{
                width: '100%',
                height: '1200px',
                border: '1px solid white',
                backgroundColor:'	LightSteelBlue'
            }}
            >
                 <div
            style={{
                width: '20%',
                height: '1200px',
                border: '1px solid  #EEE9E9',
                backgroundColor:'#EEE9E9'
            }}
            >
                
              <div className="user_left" style={{backgroundColor:'	#CDC8B1',width:'50%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} >用户信息</div>
              <div  className="user_left" style={{backgroundColor:'white',width:'60%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/object')}}>奖品设置</div>
              <div  className="user_left" style={{backgroundColor:'white',width:'70%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/ownobj')}} >获奖详情</div>  
              <button className="user_left" style={{ backgroundColor: '#CD0000', width: '40%', height: '3%', marginTop: '70%', fontSize: '1.2rem' }}  
              onClick={()=>{
                if(window.confirm('确定退出？')){
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                this.props.history.push('/login');

              }}}
              >退出账号</button>
            </div>
            

                  <div
                   style={{
                    width: '80%',
                    height: '60px',
                    borderTop: '1px solid LightSteelBlue',
                    borderBottom: '1px solid gray',
                    marginLeft:'20%',
                    marginTop:'-1200px'
                }}
                  >
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        marginLeft:'15%',
                        backgroundColor:'white',
                        marginTop:'-30px',
                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                    onClick={()=>{
                        this.props.history.push('/user')
                    }}
                   
                      >用户列表</div>
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        marginLeft:'25%',
                        float:'left',
                        backgroundColor:'white',
                        marginTop:'-83px',
                        marginLeft:'40%',
                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                    onClick={()=>{
                        this.props.history.push('/adduser')
                    }}
                      >添加用户</div>
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        marginLeft:'25%',
                        float:'left',
                        backgroundColor:'#E6E6FA',
                        marginTop:'-83px',
                        marginLeft:'65%',
                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                   
                      >编辑用户</div>
                      </div>  
                      
                  <div
                   style={{
                    width: '80%',
                    height: '1100px',
                    border: '1px solid LightSteelBlue',
                    marginLeft:'20%',
                }}
                  >
                      <div style={{width:'50%',height:'600px',marginLeft:'25%',marginTop:'150px',fontSize:'1.2rem',color:'gray'}}>
                   昵称：<div style={{width:'50%',height:'8%',marginLeft:'170px',marginTop:'-30px'}}  
                  //   onInput={(event) => {
                  //     this.setUserInfo(event, 'gamename');
                  // }}
                   > {this.state.gamename}</div><br/>
                   历史积分：<div style={{width:'50%',height:'8%',marginLeft:'130px',marginTop:'-20px'}} 
                   >
                     <button style={{width:'40px',height:'40px',marginRight:'10px'}}
                     onClick={()=>{
                       this.setState({
                         history:this.state.history+10
                       })
                     }}
                     >+</button>
                     {this.state.history}
                     <button  style={{width:'40px',height:'40px',marginLeft:'10px'}}
                     onClick={()=>{
                      this.setState({
                        history:this.state.history-10
                      })
                    }}
                     >-</button></div><br/>
                   出勤次数：<div style={{width:'50%',height:'8%',marginLeft:'130px',marginTop:'-20px'}} 
                   >
                     <button style={{width:'40px',height:'40px',marginRight:'10px'}}
                      onClick={()=>{
                        this.setState({
                         count:this.state.count+1
                        })
                      }}
                     >+</button>
                     {this.state.count}
                     <button style={{width:'40px',height:'40px',marginLeft:'10px'}}
                     onClick={()=>{
                      this.setState({
                        count:this.state.count-1
                      })
                    }}
                     >-</button></div><br/>
                   工会：<div style={{width:'50%',height:'8%',marginLeft:'150px',marginTop:'-20px'}} 
                  
                   >{this.state.orgation}</div><br/>
                   <button style={{width:'60px',marginLeft:'15%',marginRight:'180px',marginTop:'50px'}} 
                   onClick={()=>{
                    //  console.log(this.state);
                      this.putData();
                   }}
                   >提交</button>
                   {/* <button
                   onClick={()=>{
                    //  console.log(this.state);
                      // this.setState({
                      //   gamename:'',
                      //   orgation:'',
                      //   history:'',
                      //   count:'',
                      // })
                    }}
                   >取消</button> */}
                   </div>
                  </div>
                  

                {/* </div> */}
               
                
            </div>
        )
    }
}

export default updateUser
