import { ImagePicker, WingBlank } from 'antd-mobile';
import React, { Component } from 'react'
// 添加奖品

class addObj extends Component {
    constructor(props){
        super(props);
        this.state={
          obj:'',
          count:'',
          probability:'',
          img:'',
          files:[]
            
        }
    }
    setUserInfo(event, key) {
      let obj = {};
      obj[key] = event.target.value;
      this.setState(obj);
  }
  onChange = (files, type, index) => {
    let fname='';
    var time=new Date();
    let year=time.getFullYear();
    let month=time.getMonth();
    let day=time.getDate();
    let hours=time.getHours();
    let minutes=time.getMinutes();
    let second=time.getSeconds();
    fname=`${year}-${month}-${day}-${hours}-${minutes}-${second}.jpg`;
    
    console.log(fname);
    
    this.setState({
      files,
      img:'image/object/'+fname
    });
    console.log(files[0]);
    if(files[0]){  
    fetch('https://www.sevenhero.cn:2050/uploadobject',{
        method:'POST',
        body:JSON.stringify({
            filename:fname,
            url:files[0].url,
            
        })
    }).then(res=>{
      // alert('上传成功');
      // if(res.status===200){
      //   this.setState({
      //     files:[]
      //   })
      // }
    })
  // }
    }
    else{
      alert('记得上传图片');
    }
}

  putData=()=>{
    let data={
      obj:this.state.obj,
      count:this.state.count,
      probability:this.state.probability,
      img:this.state.img,
    };
    console.log(data)

    console.log(data);
    fetch('https://www.sevenhero.cn:2050/object', {
            method: 'POST',
            headers: {
                'content-type': 'text/plain'
            },
            body: JSON.stringify(data)
        }).then(async res => {

            if (!res.ok) {
                let errtext = await res.text()
                throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
            } else {
                //  return res//成功后的返回值
               return ;
            }

        }).then(d => {
            //'提交成功'
            alert('奖品添加成功')
            this.setState({
              obj:'',
              count:'',
              probability:'',
              src:'',
              files:[]
              
            })
           
        }).catch(err => {
            alert(err.message)
        })
  }
    render() {
      const { files } = this.state;
        return (
            <div
            style={{
                width: '100%',
                height: '1200px',
                border: '1px solid white',
                backgroundColor:'	LightSteelBlue'
            }}
            >
                 <div
            style={{
                width: '20%',
                height: '1200px',
                border: '1px solid #EEE9E9',
                backgroundColor:'#EEE9E9'
            }}
            >
                
              <div className="user_left" style={{backgroundColor:'white',width:'50%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/user')}}>用户信息</div>
              <div  className="user_left" style={{backgroundColor:'	#CDC8B1',width:'60%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} >奖品设置</div>
              <div  className="user_left" style={{backgroundColor:'white',width:'70%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/ownobj')}} >获奖详情</div>  
              <button className="user_left" style={{ backgroundColor: '#CD0000', width: '40%', height: '3%', marginTop: '70%', fontSize: '1.2rem' }}  
              onClick={()=>{
                if(window.confirm('确定退出？')){
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                this.props.history.push('/login');

              }}}
              >退出账号</button>
            </div>
            

                  <div
                   style={{
                    width: '80%',
                    height: '60px',
                    borderTop: '1px solid LightSteelBlue',
                    borderBottom: '1px solid gray',

                    // borderStyle:'dotted',
                    marginLeft:'20%',
                    
                    marginTop:'-1200px'
                }}
                  >
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        // border: '1px solid blue',
                        marginLeft:'20%',
                        backgroundColor:'white',

                        // float:'top',
                        // zIndex:'1',
                        marginTop:'-30px',
                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                    onClick={()=>{
                        this.props.history.push('/object')
                    }}
                      >奖品列表</div>
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        marginLeft:'50%',
                        float:'left',
                        backgroundColor:'#E6E6FA',


                        marginTop:'-83px',
                        // marginLeft:'40%',

                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                   
                      >添加奖品</div>
                      </div>

                  <div
                   style={{
                    width: '80%',
                    height: '1130px',
                    border: '1px solid LightSteelBlue',
                    marginLeft:'20%',
                    // float:'left',
                    // marginTop:'-1300px'
                }}
                  >
                     <WingBlank
      style={{
                marginTop:'150px',
                zIndex:'1',
                marginLeft:'38%',
                // border:'1px solid black'
              }}>
       <span style={{fontSize:'1.2rem',color:'gray',marginLeft:'-20%',}}>奖品图片</span>
        <ImagePicker
          files={files}
          onChange={this.onChange}
          onImageClick={(index, fs) => console.log(index, fs)}
          selectable={files.length < 1}
          multiple={this.state.multiple}
        />
      </WingBlank>
                     <div style={{width:'50%',height:'600px',marginLeft:'25%',marginTop:'50px',fontSize:'1.2rem',color:'gray'}}>
                   奖品名字：<input style={{width:'50%',height:'8%',marginTop:'20px'}}  value={this.state.obj}
                    onInput={(event) => {
                      this.setUserInfo(event, 'obj');
                  }}
                   /><br/>
                   奖品数量：<input style={{width:'50%',height:'8%',marginTop:'20px'}}  value={this.state.count}
                    onInput={(event) => {
                      this.setUserInfo(event, 'count');
                  }}
                   /><br/>
                   概率：<input style={{width:'50%',marginLeft:'50px',height:'8%',marginTop:'20px'}}  value={this.state.probability} placeholder='例如：20%,只输入20即可'
                   
                    onInput={(event) => {
                      this.setUserInfo(event, 'probability');
                  }}
                   /><br/>
                   {/* 工会：<input style={{width:'50%',height:'8%',marginLeft:'50px',marginTop:'20px'}}
                    onInput={(event) => {
                      this.setUserInfo(event, 'orgation');
                  }}
                   /> */}
                   <br/>
                   <button style={{width:'60px',marginLeft:'20%',marginRight:'180px',marginTop:'50px'}}
                   onClick={()=>{
                    //  console.log(this.state);
                      this.putData();
                   }}
                   >提交</button><button
                   onClick={()=>{
                    //  console.log(this.state);
                      this.setState({
                        obj:'',
              count:'',
              probability:'',
              src:'',
              files:[]
              
                      })
                   }}
                   >取消</button>
                   </div>
                   
                  </div>
                  

                {/* </div> */}
               
                
            </div>
        )
    }
}
export default addObj;