import React, { Component } from 'react'
// 添加用户
class addUser extends Component {
    constructor(props){
        super(props);
       this.state={
         gamename:'',
         orgation:'',
         history:'',
         count:'',
         can:5
            
        }
    }
    setUserInfo(event, key) {
      let obj = {};
      obj[key] = event.target.value;
      this.setState(obj);
  }
  putData=()=>{
    let data=this.state;
    console.log(data);
    fetch('https://www.sevenhero.cn:2050/users', {
            method: 'POST',
            headers: {
                'content-type': 'text/plain'
            },
            body: JSON.stringify(data)
        }).then(async res => {

            if (!res.ok) {
                let errtext = await res.text()
                throw new Error(`${res.status} : ${errtext}`)//错误的状态码和内容
            } else {
                //  return res//成功后的返回值
               return ;
            }

        }).then(d => {
            //'提交成功'
            alert('用户添加成功')
            this.setState({
              gamename:'',
         orgation:'',
         history:'',
         count:'',
              
            })
            // this.props.history.push('/adduser')
           
        }).catch(err => {
            alert(err.message)
        })
  }
    render() {
        return (
            <div
            style={{
                width: '100%',
                height: '1200px',
                border: '1px solid white',
                backgroundColor:'	LightSteelBlue'
            }}
            >
                 <div
            style={{
                width: '20%',
                height: '1200px',
                border: '1px solid #EEE9E9',
                backgroundColor:'#EEE9E9'
            }}
            >
                
              <div className="user_left" style={{backgroundColor:'	#CDC8B1',width:'50%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} >用户信息</div>
              <div  className="user_left" style={{backgroundColor:'white',width:'60%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/object')}}>奖品设置</div>
              <div  className="user_left" style={{backgroundColor:'white',width:'70%',height:'3%',marginTop:'20%',fontSize:'1.2rem',paddingLeft:'26%',paddingTop:'5%'}} onClick={()=>{this.props.history.push('/ownobj')}} >获奖详情</div>  
              <button className="user_left" style={{ backgroundColor: '#CD0000', width: '40%', height: '3%', marginTop: '70%', fontSize: '1.2rem' }}  
              onClick={()=>{
                if(window.confirm('确定退出？')){
                localStorage.removeItem('token')
                localStorage.removeItem('userinfo')
                this.props.history.push('/login');

              }}}
              >退出账号</button>
            </div>
            

                  <div
                   style={{
                    width: '80%',
                    height: '60px',
                    borderTop: '1px solid LightSteelBlue',
                    borderBottom: '1px solid gray',
                    marginLeft:'20%',
                    
                    marginTop:'-1200px'
                }}
                  >
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        // border: '1px solid blue',
                        marginLeft:'20%',
                        backgroundColor:'white',

                        // float:'top',
                        // zIndex:'1',
                        marginTop:'-30px',
                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                    onClick={()=>{
                        this.props.history.push('/user')
                    }}
                      >用户列表</div>
                      <div
                       style={{
                        width: '12%',
                        height: '40px',
                        marginLeft:'50%',
                        float:'left',
                        backgroundColor:'#E6E6FA',


                        marginTop:'-83px',
                        // marginLeft:'40%',

                        fontSize:'1.2rem',
                      paddingTop:'40px',
                      paddingLeft:'60px',

                    }}
                   
                      >添加用户</div>
                      </div>

                  <div
                   style={{
                    width: '80%',
                    height: '1130px',
                    border: '1px solid LightSteelBlue',
                    marginLeft:'20%',
                    // float:'left',
                    // marginTop:'-1300px'
                }}
                  >
                    <div style={{width:'50%',height:'600px',marginLeft:'25%',marginTop:'150px',fontSize:'1.2rem',color:'gray'}}>
                   昵称：<input style={{width:'50%',height:'8%',marginLeft:'50px',marginTop:'20px'}}   value={this.state.gamename}
                    onInput={(event) => {
                      this.setUserInfo(event, 'gamename');
                  }}
                   /><br/>
                   历史积分：<input style={{width:'50%',height:'8%',marginTop:'20px'}} value={this.state.history}
                    onInput={(event) => {
                      this.setUserInfo(event, 'history');
                  }}
                   /><br/>
                   出勤次数：<input style={{width:'50%',height:'8%',marginTop:'20px'}} value={this.state.count}
                    onInput={(event) => {
                      this.setUserInfo(event, 'count');
                  }}
                   /><br/>
                   工会：<input style={{width:'50%',height:'8%',marginLeft:'50px',marginTop:'20px'}} value={this.state.orgation}
                    onInput={(event) => {
                      this.setUserInfo(event, 'orgation');
                  }}
                   /><br/>
                   <button style={{width:'60px',marginLeft:'20%',marginRight:'180px',marginTop:'50px'}} 
                   onClick={()=>{
                    //  console.log(this.state);
                      this.putData();
                   }}
                   >提交</button><button
                   onClick={()=>{
                    //  console.log(this.state);
                      this.setState({
                        gamename:'',
                        orgation:'',
                        history:'',
                        count:'',
                      })
                    }}
                   >取消</button>
                   </div>
                  </div>
                  

                {/* </div> */}
               
                
            </div>
        )
    }
}

export default addUser
